import React, { useState } from 'react';
import "./File.css"
import logo from "../img/alan.png"
import img1 from "../img/2.jpg"
import img2 from "../img/3.jpg"




function Home() {
    const [selected, setSelected] = useState(img1)
    const [Text, setText] = useState("Home")
    function txt1() {
        setSelected(img1);
        setText("Home");
    }
    function txt2() {
        setSelected(img2);
        setText("About");
    }


    return (
        <div>
            <div className="nav">
                <div>
                    <img src={logo} className="nav-img" ></img>
                </div>
                <div className="nav-text">
                    <a href="#" className="text" onClick={txt1}>HOME</a>
                    <a href="#" className="text" onClick={txt2}>ABOUT</a>
                    <a href="#" className="text">PROJECTS</a>
                    <a href="#" className="text">SERVICES</a>
                    <a href="#" className="text">BLOGS</a>
                    <a href="#" className="text">PAGES</a>
                    <a href="#" className="text">CONTACT</a>
                </div>
                <div className="nav-but">
                    <div className="nav-bu"><h4 className="t1">GET IN TOUCH</h4></div>
                </div>
            </div>
            <div className="nav2">
                <img src={selected} className="img1" ></img>
                <div class="txt2">
                    <h1>{Text}</h1>

                </div>
            </div>
        </div >
    );

}

export default Home;